.loginsignup
{
    width: 100%;
    height: 150vh;
    background: #fce3fe;
    padding-top:100px;
}
.logsig-cont{
    width:580px;
    height:600px;
    background: white;
    margin :auto;
    padding: 40px 60px;
}
.logsig-cont h1{
    margin: 20px 0px;
}
.loginsign-fields{
    display:flex;
    flex-direction: column;
    gap:29px;
    margin-top: 30px;
}
.loginsign-fields input{
    width:100%;
    height: 72px;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    color: #5c5c5c;
    font-size: 18px;

}
.logsig-cont button{
    margin-top: 25px;
    width: 580px;
    height: 72px;
    color: white;
    background: #ff4141;
    border: none;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
}
.loginsig-log{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
.logsig-cont span{
    color: #ff4141;
    cursor: pointer;

}
.loginsig-agree{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

