.newsletter{
width: 65%;
height:40vh;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
margin :auto;
padding: 0px 140px;
margin-bottom: 150px;
background: linear-gradient(180deg, #fde1ff,#e1ffea22 60%);
gap:30px;
margin-top: 400px;
/* margin-bottom: 100px; */
}
 .newsletter h1{
    color:#454545;
    font-size: 50px;
    font-weight:600;
    margin-top: 50px;
 }

.newsletter p{
    color: #454545;
    font-size: 20px;
    font-weight: 400;
}

.newletter div{
    display: flex;
    align-items:center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border:1px solid #e3e3e3;
}

.newsletter input{
    width:500px;
    height: 50px;
    margin-left: 30px;
    border:1px solid #e3e3e3;
    border-radius: 80px;
    /* border:none; */
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
    margin-top: 20px;

}

.newsletter div button{
    width:200px;
    height: 50px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
} 
