.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    /* margin-top: 150px; */
    margin-bottom: 30px;
    background: linear-gradient(180deg, #fde1ff,#e1ffea22 60%);
}
.offers-left{
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.offers-left p{color: #171717;
    font-size: 15px;
    font-weight: 600;

}
.offers-left button{
    width: 200px;
    height: 50px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    display: flex;
    /* align-items: center; */
    justify-content: flex-end;
    padding-top: 50px;
}