.fotter{
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    margin-top: 50px;
}
.fotter-logo{
    display: flex;
    align-items: center;
    gap:20px;
}
.fotter-logo p{
    color:#383838;
    font-size: 46px;
    font-weight: 700;
}
.fotter-links{
    display: flex;
    list-style: none;
    gap:50px;
    color: #252525;
    font-size: 20px;
}
.fotter-links li{
    cursor: pointer;
}
.fotter-social-icon{
    display: flex;
    gap:20px;
}
.fotter-icons-container{
    cursor: pointer;
    padding: 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 1px solid #c3c3c3;
    border-radius: 50% 80%/70% 10%;
}

.fotter-copy-right{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:30px;
    width:100%;
    margin-bottom: 30px;
    color:#1a1a1a;
    font-size: 20px;

}
.fotter-copy-right hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height:3px;
    background: #c7c7c7;
}